function initialize() {
    var mp = new BMap.Map('map');
    // mp.centerAndZoom(new BMap.Point(121.491, 31.233), 11);
    mp.centerAndZoom("", 12);
    mp.enableScrollWheelZoom(true);
}

function loadScript() {
    var script = document.createElement("script");
    script.src = "http://api.map.baidu.com/api?v=2.0&ak=rNk7ejzYGoCt36SY8LAuQbXlZuWLDXIV&callback=initialize"; //v2.0
    // http://api.map.baidu.com/api?v=1.4&ak=&callback=initialize"; //v1.4
    document.body.appendChild(script);
}

window.onload = loadScript;
